/* critical:start */
.p-login {
  background: #373e42 center center no-repeat;
  background-size: cover;
  color: #fff;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  padding-bottom: 40px; }
  @media (max-width: 767px) {
    .p-login {
      background-image: url("../../common/img/bg-forest-ipad.jpg"); } }
  @media (min-width: 768px) and (max-width: 1199px) {
    .p-login {
      background-image: url("../../common/img/bg-forest-medium.jpg"); } }
  @media (min-width: 1200px) {
    .p-login {
      background-image: url("../../common/img/bg-forest-max.jpg"); } }
  .p-login::before {
    content: '';
    display: block;
    margin-bottom: auto; }

.p-login__main {
  max-width: 380px;
  margin: 40px auto;
  position: relative; }
  @media (max-width: 319px) {
    .p-login__main {
      min-width: calc(100% - 20px);
      width: 300px; } }
  @media (min-width: 320px) and (max-width: 767px) {
    .p-login__main {
      min-width: 300px;
      width: 93.75%;
      max-width: 348px; } }
  @media (min-width: 768px) and (max-width: 1199px) {
    .p-login__main {
      min-width: 348px;
      width: 45.3125%;
      max-width: 380px; } }
  @media (min-width: 1200px) {
    .p-login__main {
      width: 98%; } }

/* critical:end */



